// a little function to help us with reordering the result
const reorder = (list, startIndex, endIndex) => {
  // console.log(list);
  // console.log(startIndex);
  // console.log(endIndex);
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

/**
 * Moves an item from one list to another list.
 */
const move = (
  state,
  source,
  destination,
  droppableSource,
  droppableDestination
) => {
  const sourceClone = Array.from(source);
  const destClone = Array.from(destination);
  const [removed] = sourceClone.splice(droppableSource.index, 1);

  destClone.splice(droppableDestination.index, 0, removed);

  const result = {};

  let terms = state.terms.map((term) => {
    if (term.id === droppableSource.droppableId) {
      term.courseList = sourceClone;
    }
    if (term.id === droppableDestination.droppableId) {
      term.courseList = destClone;
    }
    return term;
  });

  result.terms = terms;

  return result;
};

const grid = 8;

const getItemStyle = (isDragging, draggableStyle) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: "none",
  padding: grid * 2,
  margin: `0 0 ${grid}px 0`,
  borderRadius: "4px",

  // change background colour if dragging
  // background: isDragging ? "#333" : "#555",

  // styles we need to apply on draggables
  ...draggableStyle
});

const getListStyle = (isDraggingOver) => ({
  background: isDraggingOver ? "#444" : "#222",
  borderRadius: "8px",
  padding: grid,
  margin: "2px",
  width: 250
});

// fetch items in each column
class App extends Component {
  state = {
    terms: [
      {
        id: "term1",
        name: "Term 1",
        includeInTotal: true,
        courseList: courseList1,
        creditTotal: 0
      },
      {
        id: "term2",
        name: "Term 2",
        includeInTotal: true,
        courseList: courseList2,
        creditTotal: 0
      },
      {
        id: "term3",
        name: "Term 3",
        includeInTotal: true,
        courseList: courseList3,
        creditTotal: 0
      },
      {
        id: "term4",
        name: "Term 4",
        includeInTotal: true,
        courseList: courseList4,
        creditTotal: 0
      },
      {
        id: "term5",
        name: "Term 5",
        includeInTotal: true,
        courseList: courseList5,
        creditTotal: 0
      },
      {
        id: "bin",
        name: "Bin",
        includeInTotal: false,
        courseList: courseList6,
        creditTotal: 0
      }
    ],
    creditTotals: {
      firstSemester: 0,
      secondSemester: 0,
      thirdSemester: 0,
      fourthSemester: 0,
      fifthSemester: 0
    }
    // firstSemester: courseList1,
    // secondSemester: courseList2,
    // thirdSemester: courseList3,
    // fourthSemester: courseList4,
    // fifthSemester: courseList5,
    // sixthSemester: courseList6
  };

  componentDidMount() {
    this.calculateCourseCreditTotal(this.state);
  }

  /** Convert IDs to a list */

  id2List = {
    firstSemester: "firstSemester",
    secondSemester: "secondSemester",
    thirdSemester: "thirdSemester",
    fourthSemester: "fourthSemester",
    fifthSemester: "fifthSemester",
    sixthSemester: "sixthSemester"
  };

  calculateCourseCreditTotal() {
    // Calculate the total here and return it.

    let terms = this.state.terms.map((term) => {
      let courseCredits = term.courseList.map((course) => course.credits);

      let totalCourseCreditsForTerm = 0;

      if (courseCredits.length) {
        totalCourseCreditsForTerm = courseCredits.reduce((n, val) => {
          return n + val;
        });
      }

      term.creditTotal = totalCourseCreditsForTerm;

      return term;
    });

    let state = {
      terms
    };

    this.setState(state);
  }

  getList = (id) => {
    // console.log("idList", this.id2List);
    // console.log("state", this.state);
    // console.log("id", id);
    // console.log("list", this.state[this.id2List[id]]);
    return this.state.terms.find((term) => term.id === id).courseList;
  };

  onDragEnd = (result) => {
    const { source, destination } = result;

    // console.log('end', source, destination);
    // console.log("source", source);
    // console.log("dest", destination);

    // dropped outside the list
    if (!destination) {
      return;
    }

    if (source.droppableId === destination.droppableId) {
      const items = reorder(
        this.getList(source.droppableId),
        source.index,
        destination.index
      );

      // console.log("items", items);

      // Set state
      let state = { items };

      let terms = this.state.terms.map((term) => {
        if (term.id === source.droppableId) {
          term.courseList = items;
        }
        return term;
      });

      state.terms = terms;

      this.setState(state);
    } else {
      //console.log("source", source.droppableId);
      //console.log("destination", destination.droppableId);
      const result = move(
        this.state,
        this.getList(source.droppableId),
        this.getList(destination.droppableId),
        source,
        destination
      );

      //console.log("result", result);
      this.setState(result, () => this.calculateCourseCreditTotal());
    }
  };

  onAddTerm = () => {
    let state = {
      terms: [
        ...this.state.terms,
        {
          id:
            "term" +
            (this.state.terms.filter((t) => t.includeInTotal).length + 1),
          name:
            "Term " +
            (this.state.terms.filter((t) => t.includeInTotal).length + 1),
          courseList: [],
          creditTotal: 0,
          includeInTotal: true
        }
      ]
    };

    this.setState(state);
  };

  deleteTerm(termToDelete) {
    let state = {};

    let courseList = [...termToDelete.courseList];

    let terms = this.state.terms.filter(
      (term) => termToDelete.id !== term.id && term.includeInTotal
    );
    let bin = this.state.terms.find((term) => !term.includeInTotal);

    bin.courseList = [...bin.courseList, ...courseList];

    let newTerms = terms.map((term, index) => {
      console.log(term);
      term.id = "term" + (index + 1);
      term.name = "Term " + (index + 1);
      return term;
    });

    newTerms.push(bin);

    state.terms = newTerms;

    this.setState(state, this.calculateCourseCreditTotal());
  }

  renderTerm(term) {
    return (
      <Droppable droppableId={term.id}>
        {(provided, snapshot) => (
          <div
            className="semesterBlock"
            ref={provided.innerRef}
            style={getListStyle(snapshot.isDraggingOver)}
          >
            <div className="semesterName" onClick={() => this.deleteTerm(term)}>
              {term.name}
            </div>
            {term.courseList.map((course, index) => (
              <Draggable
                key={course.id}
                draggableId={course.id.toString()}
                index={index}
              >
                {(provided, snapshot) => (
                  <div
                    className={["courseBlock", course.category].join(" ")}
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    style={getItemStyle(
                      snapshot.isDragging,
                      provided.draggableProps.style
                    )}
                  >
                    <b>{course.name}</b>
                    &nbsp;
                    <span className="className">{course.content}</span>
                    <span className="creditNum">
                      {course.credits} credits â€¢ {course.difficulty}
                      /5 difficulty
                    </span>
                  </div>
                )}
              </Draggable>
            ))}
            <div className="totalCredits">{term.creditTotal} Credits</div>
            <div className="totalCost">
              {(term.creditTotal * 2035).toLocaleString("en-US", {
                style: "currency",
                currency: "USD",
                minimumFractionDigits: 0,
                maximumFractionDigits: 0
              })}
            </div>
          </div>
        )}
      </Droppable>
    );
  }

  // Normally you would want to split things out into separate components.
  // But in this example everything is just done in one place for simplicity
  render() {
    return (
      <React.Fragment>
        <div className="allBlocks">
          <DragDropContext onDragEnd={this.onDragEnd}>
            {this.state.terms
              .filter((t) => t.includeInTotal)
              .map((term, index) => this.renderTerm(term))}
            <AddTerm onAddTerm={() => this.onAddTerm()} />
            {this.state.terms
              .filter((t) => !t.includeInTotal)
              .map((term, index) => this.renderTerm(term))}
          </DragDropContext>
        </div>
        <div className="course-credit-total">
          Degree Credit Total:{" "}
          {this.state.terms
            .filter((t) => t.includeInTotal)
            .map((t) => t.creditTotal)
            .reduce((a, b) => a + b)}
          /38
        </div>
      </React.Fragment>
    );
  }
}

// Put the things into the DOM!
ReactDOM.render(<App />, document.getElementById("root"));
